@charset "UTF-8";
*,
*::after,
*::before {
  background-repeat: no-repeat;
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}
@media (max-width: 71.8125em) {
  html {
    font-size: 56.25%;
  }
}
@media (max-width: 39.9375em) {
  html {
    font-size: 31.25%;
  }
}

body {
  background-color: #212730;
  line-height: 1.2;
  color: white;
  font-family: var(--f-rale);
  overflow-x: hidden;
}

:root {
  --c--t1: hsl(354, 90%, 65%);
  --c--t2: hsl(0, 0%, 100%);
  --c--text: hsl(217, 12%, 67%);
  --c-quote: hsl(0, 0%, 93%);
  --f-mon: "Montserrat", sans-serif;
  --f-open: "Open Sans", sans-serif;
  --f-rale: "Raleway", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

input,
button,
textarea,
select {
  outline: none;
  border: 0;
}

/* Chỉ cho phép textarea được kéo dãn theo chiều dọc, trông sẽ đẹp hơn, không làm xấu layout */
textarea {
  resize: vertical;
}

/* Xóa bỏ khoảng trắng thừa dưới image */
img {
  vertical-align: middle;
}

/* Cũng để dễ control theo design, nên mình thường reset luôn ul, ol */
ul,
ol {
  list-style: none;
}

/* Tạo hiệu ứng bôi đen text khác biệt hơn mặc định */
::selection {
  background: #0031a2;
  color: #fff;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.first-letter-uppercase {
  display: block;
}

.first-letter-uppercase:first-letter {
  text-transform: uppercase;
}

.caption {
  font-size: 1.125rem;
  font-family: var(--f-open);
  color: var(--c--t1);
  text-transform: uppercase;
  font-weight: 700;
  line-height: 2;
}

.heading {
  font-size: 2.25rem;
  font-weight: 800;
  line-height: 1.556;
  margin-top: 2.2495rem;
  margin-bottom: 1.6869375rem;
}

.text {
  font-size: 1.125rem;
  font-family: var(--f-open);
  color: var(--c--text);
  font-weight: 400;
  line-height: 1.889;
  opacity: 0.8;
}

.btn {
  display: inline-block;
  background-image: linear-gradient(0deg, #f66767 0%, #f68826 100%);
  border-radius: 0.3125rem;
  font-family: var(--f-open);
  font-weight: 700;
  line-height: 1.2;
  padding: 1.25rem 4.375rem;
}
.btn--big {
  font-size: 1.5rem;
  padding: 2.0625rem 4.75rem;
}

.wrapper-all {
  max-width: 120rem;
  margin: 0 auto;
  background: url("../app/img/bg.jpg") no-repeat top/100%;
}

.container {
  max-width: 75rem;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 0.9375rem;
  padding-bottom: 0;
  padding-left: 0.9375rem;
}
.container--supper {
  padding-top: 17.5rem;
  padding-bottom: 24.375rem;
}
@media (max-width: 71.8125em) {
  .container--supper {
    padding-top: 9.375rem;
    padding-bottom: 15.625rem;
  }
}
@media (max-width: 39.9375em) {
  .container--supper {
    padding-top: 3.125rem;
    padding-bottom: 6.25rem;
  }
}
.container--small {
  padding-top: 9.375rem;
  padding-bottom: 12.5rem;
}
.container--bt {
  padding-bottom: 12.5rem;
}
@media (max-width: 71.8125em) {
  .container--bt {
    padding-bottom: 6.25rem;
  }
}
@media (max-width: 39.9375em) {
  .container--bt {
    padding-bottom: 4.1666666688rem;
  }
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none !important;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none !important;
  }
}

.header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.589);
  padding: 2.5rem 0;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__menu ul {
  display: flex;
  align-items: center;
  gap: 0 3.75rem;
}
.header__menu ul li {
  font-weight: 400;
  font-family: var(--f-open);
  text-transform: capitalize;
}
.header__tools {
  display: flex;
  align-items: center;
  gap: 0 4.375rem;
}
.header__tools img {
  width: 30px;
}

.hero {
  display: block;
}
.hero__main h1 {
  font-size: 7.5rem;
  font-weight: 800;
  text-transform: uppercase;
}
.hero__main h3 {
  font-weight: 400;
  text-transform: capitalize;
  font-size: 2.5rem;
}
.hero__play {
  display: flex;
  align-items: center;
  gap: 0 1.875rem;
  margin-top: 3.3875rem;
}
@media (max-width: 39.9375em) {
  .hero__play img {
    width: 20px;
  }
}
.hero__play p {
  font-size: 1.125rem;
  font-family: var(--f-open);
  font-weight: 400;
}

.features__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 1.875rem;
}
@media (max-width: 39.9375em) {
  .features__list {
    display: grid;
    justify-content: flex-start;
    grid-auto-columns: 150px;
    grid-auto-flow: column;
    gap: 1.5rem;
    overflow: auto;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    scroll-snap-stop: always;
    touch-action: manipulation;
    -webkit-overflow-scrolling: touch;
    scroll-padding: 1rem;
    grid-template-columns: unset;
  }
  .features__list::-webkit-scrollbar {
    display: none;
    width: 0;
  }
  .features__list > * {
    scroll-snap-align: start;
  }
}
.features__item {
  flex-basis: calc(100% / 3);
  text-align: center;
  padding: 4.6875rem 2.625rem 4.375rem;
  border-radius: 0.625rem;
  background-color: #2b3139;
}
@media (max-width: 39.9375em) {
  .features__item img {
    width: 30px;
  }
}
.features__item h3 {
  margin-top: 2.2186875rem;
  font-size: 1.375rem;
  line-height: 1.591;
  font-weight: 800;
}
.features__item p {
  margin-top: 1.218625rem;
}

.about .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 6.25rem;
}
@media (max-width: 71.8125em) {
  .about .container {
    flex-direction: column;
    justify-content: center;
  }
}
.about__left {
  max-width: 29.375rem;
}
@media (max-width: 71.8125em) {
  .about__left {
    text-align: center;
  }
}
.about__left a {
  margin-top: 3.5624375rem;
}
.about__image {
  position: relative;
  max-width: 37.5rem;
}
@media (max-width: 71.8125em) {
  .about__image {
    text-align: center;
    max-width: none;
  }
}
@media (max-width: 39.9375em) {
  .about__image {
    display: none;
  }
}
.about__img {
  left: 48%;
  position: absolute;
  top: 54%;
  transform: translate(-50%, -50%);
}

.work {
  text-align: center;
}
.work__title {
  max-width: 31.25rem;
  margin: 0 auto;
  margin-bottom: 5.7499375rem;
}
.work__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.875rem;
  grid-template-rows: 23.125rem 10.625rem 23.125rem;
}
.work__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 0.625rem;
}
.work__item:nth-child(1) {
  grid-row: 1/2;
}
.work__item:nth-child(2) {
  grid-row: 1/3;
}
.work__item:nth-child(3) {
  grid-row: 2/4;
}
.work__item:nth-child(4) {
  grid-row: 3/4;
}
.work a.btn {
  margin-top: 4.375rem;
}

.experience .container {
  gap: 0 1.875rem;
  display: flex;
  align-items: center;
}
.experience span {
  position: relative;
  text-align: center;
  flex-basis: 41.875rem;
  font-size: 25rem;
  font-family: var(--f-open);
  font-weight: 800;
  color: #6879b6;
  text-transform: uppercase;
  line-height: 0.088;
  padding: 9.0625rem 0;
  background-image: url("./../app/img/experience/1.png");
  background-position-x: 20%;
  background-position-y: 45%;
  background-repeat: no-repeat;
  background-size: 94%;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}
.experience span::after {
  content: "";
  position: absolute;
  background-image: url("./../app/img/experience/2.png");
  left: 55%;
  height: 100%;
  width: 100%;
  z-index: -1;
}
.experience__title {
  flex-basis: 29.375rem;
}

.parner .container {
  text-align: center;
}
.parner__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 5.6875rem;
  gap: 1.875rem;
}
.parner__item {
  display: grid;
  place-items: center;
  flex-basis: calc((100% - (3 * 30 * 0.0625rem)) / 4);
  height: 11.25rem;
  border-radius: 0.625rem;
  background-color: #2b3139;
}
@media (max-width: 71.8125em) {
  .parner__item {
    flex-basis: calc((100% - (3 * 30 * 0.0625rem)) / 2);
  }
}

.testimonials {
  text-align: center;
  padding-bottom: 30.625rem;
}
.testimonials__title {
  max-width: 31.25rem;
  margin: 0 auto;
}
.testimonials__list {
  margin-top: 4.4999375rem;
}
.testimonials__item {
  position: relative;
  padding: 4.875rem 6.25rem;
  width: 51.875rem;
  border-radius: 0.625rem;
  background-color: #222831;
  box-shadow: 0 30px 70px 0 rgba(40, 47, 58, 0.5);
  opacity: 0.3;
}
.testimonials__item.is-selected {
  opacity: 1;
}
.testimonials__item:not(:last-child) {
  margin-right: 4.375rem;
}
.testimonials__item::after {
  content: "";
  position: absolute;
  background-image: url("../../../app/img/testimon/quote.png");
  z-index: 9;
  top: 0;
  background-repeat: no-repeat;
  left: 50%;
  transform: translateX(-50%);
  width: 3.125rem;
  height: 3.125rem;
  background-size: 100%;
}
.testimonials__item .quote {
  font-size: 1.875rem;
  font-family: var(--f-open);
  color: #eeeeee;
  line-height: 1.6;
  font-weight: 400;
}
.testimonials__item .img-temp {
  display: inline-block;
  width: 3.125rem;
  height: 3.125rem;
  background-color: gray;
  border-radius: 9999px;
  margin-top: 2.5625rem;
}
.testimonials__item .name {
  font-size: 1.125rem;
  color: #a0a8b5;
  font-weight: 700;
  line-height: 1.278;
  margin-top: 1.718625rem;
}

.start {
  transform: translateY(45%);
}
.start__main {
  text-align: center;
  position: relative;
  padding: 9.0625rem 20.3125rem;
  border-radius: 1.875rem;
  background: url("../../app/img/start/1.png") no-repeat cover/100%;
  background-color: #1e242e;
  z-index: 9;
}
.start__main h2 {
  font-size: 3.75rem;
  font-weight: 700;
  line-height: 1.267;
}
.start__main p {
  font-size: 1.5rem;
  font-family: var(--f-open);
  color: #a0a8b5;
  line-height: 1.5;
  margin-top: 2.874375rem;
}
.start__main a.btn.btn--big {
  margin-top: 3.6875rem;
}

.footer {
  height: 31.25rem;
  background-color: grey;
}